.about-content {
  background-color: #fff;
  padding-bottom: 80px;
  margin-top: 104px;
}

.about-feautures_wrap {
  display: flex;
  justify-content: space-between;
  padding-bottom: 80px;
  margin-bottom: 80px;
  border-bottom: 2px solid #E9E9E9;
}

.about-feautures_wrap .column {
  width: 23%;
  position: relative;
}

.about-feautures_wrap .column img {
  display: block;
  width: 42px;
  height: 42px;
  object-fit: contain;
  margin-bottom: 30px;
}

.about-feautures_wrap .column h4 {
  font-size: 22px;
  line-height: 30px;
  margin-bottom: 4px;
}

.about-feautures_wrap .column p {
  font-size: 15px;
  line-height: 22px;
  color: #797C84;
}

@media screen and (max-width: 990px) {
  .about-feautures_wrap {
    flex-wrap: wrap;
  }
  .about-feautures_wrap .column {
    width: 48%;
    margin-bottom: 30px;
  }
  .about-feautures_wrap .column:nth-last-child(2), .about-feautures_wrap .column:last-child {
    margin-bottom: 0;
  }
}

@media screen and (max-width: 500px) {
  .about-content {
    padding-bottom: 30px;
    margin-top: 79px;
  }
  .about-feautures_wrap {
    padding-bottom: 30px;
    margin-bottom: 30px;
  }
  .about-feautures_wrap .column {
    width: 100%;
  }
  .about-feautures_wrap .column:nth-last-child(2) {
    margin-bottom: 30px;
  }
  .about-feautures_wrap .column img {
    width: 32px;
    height: 32px;
    margin-bottom: 15px;
  }
  .about-feautures_wrap .column h4 {
    font-size: 21px;
    line-height: 30px;
  }
  .about-feautures_wrap .column p {
    font-size: 14px;
    line-height: 20px;
  }
}

